.person__name {
  @media (min-width: $desktops-4) {
    color: $color-default-white;
    font-size: 16px;
    font-style: italic;
    font-weight: 400;
    line-height: 19px;
    letter-spacing: 0px;
    text-align: right;
    display: block;
    margin-top: 10px;
  }

  @media (max-width: $desktops-3) {
    font-size: 12px;
    font-style: italic;
    font-weight: 400;
    line-height: 14px;
    letter-spacing: 0px;
    text-align: right;
    color: $color-default-white;
    font-style: italic;
    text-align: right;
    display: block;
  }

  @media (max-width: 600px) {
    font-size: 12px;
    font-style: italic;
    font-weight: 400;
    line-height: 14px;
    letter-spacing: 0px;
    text-align: right;
    color: $color-default-white;
    display: block;
    margin-bottom: 10px;
  }
}

.person {
  h3 {
    position: relative;

    @media (min-width: $desktops) {
      &:before {
        content: url("../img/heading.svg");
        position: absolute;
        left: -26px;
        top: -20px;
        z-index: -1;
      }
    }
  }

  &--even {
    @media (min-width: $desktops) {
      position: relative;

      padding-left: 3%;
      margin-top: 204px;

      width: 65%;
      margin-right: 1.5%;

      // padding-left: 6%;
      // margin-top: 115px;

      // width: 64%;
      // margin-right: 2.5%;

      &:before {
        position: absolute;
        content: "";
        background-image: url("../img/Img_Rhomb-1024-silver.png");
        width: 98px;
        height: 98px;
        top: -195px;
        left: 4%;
      }

      &:after {
        position: absolute;
        content: "";
        background-image: url("../img/Img_Rhomb-1024-gold.png");
        width: 133px;
        height: 127px;
        top: -100px;
        left: 26%;
      }

      .person__content {
        align-items: flex-end;
      }

      .person__pic-wrapper {
        margin-right: 4%;
      }

      .person__name {
        text-align: left;
        margin-bottom: 10px;
        margin-top: 0;
      }

      .person__texts {
        margin-bottom: 30px;
      }
    }

    @media (min-width: $desktops-2) {
      // width: 68%;
      // margin-right: 2%;

      padding-left: 6%;
      margin-top: 115px;

      width: 71%;
      margin-right: 2%;

      &:before {
        position: absolute;
        content: "";
        background-image: url("../img/Img_Rhomb-1366-silver.png");
        width: 140px;
        height: 140px;
        top: -160px;
        left: 7%;
        background-repeat: no-repeat;
      }

      &:after {
        position: absolute;
        content: "";
        background-image: url("../img/Img_Rhomb-1366-gold.png");
        width: 160px;
        height: 160px;
        top: -64px;
        left: 33%;
        background-repeat: no-repeat;
      }

      .person__texts {
        width: 340px;
        margin-bottom: 40px;
      }
    }

    @media (min-width: $desktops-4) {
      width: 64%;
      margin-right: 2.5%;
      padding-left: 5%;
      margin-top: 200px;

      .person__texts {
        width: 465px;
        margin-bottom: 75px;
      }

      &:before {
        position: absolute;
        content: "";
        background-image: url("../img/star_rhomb.png");
        width: 140px;
        height: 140px;
        top: -220px;
        left: 12%;
      }

      &:after {
        position: absolute;
        content: "";
        background-image: url("../img/Img_Rhomb-04.png");
        width: 200px;
        height: 200px;
        top: -70px;
        left: 36%;
      }
    }

    // @media (min-width: $desktops-2) {

    // }

    @media (min-width: $desktops-4) {}

    @media (max-width: 600px) {
      margin-top: -36px;
    }
  }

  &--odd {
    @media (min-width: $desktops) {
      position: relative;
      width: 33.5%;

      &:before {
        position: absolute;
        content: "";
        background-image: url("../img/mirror-small.png");
        width: 152px;
        height: 219px;
        top: 125px;
        right: -7%;
        background-repeat: no-repeat;
      }

      .person__content {
        align-items: flex-end;
      }

      .person__pic-wrapper {
        margin-right: 4%;
      }

      .person__texts {
        position: absolute;
        z-index: 1;
        width: 306px;
        margin-top: 12px;
        transform: translateX(-75%);
      }

      .person__name {
        text-align: left;
      }
    }

    @media (min-width: $desktops-2) {
      // width: 31%;
      width: 32.5%;

      &:before {
        position: absolute;
        content: "";
        background-image: url("../img/mirror-medium.png");
        width: 171px;
        height: 248px;
        top: 147px;
        right: -9%;
        z-index: 2;
        background-repeat: no-repeat;
      }

      .person__texts {
        width: 306px;
        margin-top: 12px;
        position: absolute;
        transform: translateX(-100%);
      }
    }

    @media (min-width: $desktops-4) {
      width: 33.5%;

      &:before {
        position: absolute;
        content: "";
        background-image: url("../img/mirror-big.png");
        width: 265px;
        height: 384px;
        top: 180px;
        right: -7%;
        background-repeat: no-repeat;
      }

      .person__texts {
        margin-bottom: 92px;
        width: 480px;
        position: absolute;
        transform: translateX(-88%);
        margin-top: 60px;
      }
    }

    @media (max-width: $desktops-3) {}
  }

  &--last {
    @media (min-width: $desktops-2) {
      .person__name {
        margin-top: 10px;
        margin-bottom: 14px;
      }

      .person__pic-wrapper {
        transform: translateY(-24px);
      }
    }

    @media (min-width: $desktops-4) {
      .person__pic-wrapper {
        transform: translateY(0);
      }
    }
  }

  @media (max-width: 600px) {
    margin-top: 60px;

    &--odd {
      .person__pic {
        margin-left: auto;
      }

      .person__pic-wrapper {
        position: relative;

        &:before {
          position: absolute;
          content: "";
          background-image: url("../img/star_gold.png");
          width: 200px;
          height: 200px;
          background-repeat: no-repeat;
          top: 50%;
          right: 20%;
          transform: translate(-50%, -50%);
        }

        .person__name {
          text-align: right;
        }
      }
    }

    &--even {
      margin-top: 45px;

      .person__pic-wrapper {
        position: relative;

        &:before {
          position: absolute;
          content: "";
          background-image: url("../img/star_gold_2.png");
          width: 180px;
          height: 180px;
          background-repeat: no-repeat;
          top: 30%;
          right: 50%;
          transform: translate(95%, -50%);
        }
      }

      .person__texts {
        margin-top: -44px;
      }

      .person__name {
        text-align: left;
      }
    }

    &--odd:last-of-type {
      margin-top: 98px;

      .person__pic-wrapper {
        &:before {
          position: absolute;
          content: "";
          background-image: url("../img/mirror.png");
          width: 140px;
          height: 195px;
          background-repeat: no-repeat;

          top: 50%;
          left: 32%;
          z-index: 1;
          transform: translate(-50%, -50%);
        }
      }

      .person__name {
        margin-top: 20px;
        margin-bottom: 5px;
      }
    }
  }
}

.person__pic {
  @media (min-width: $desktops) {
    width: 226px;
    height: 319px;
  }

  @media (min-width: $desktops-2) {
    width: 276px;
    height: 390px;
  }

  @media (min-width: $desktops-4) {
    // width: 355px;
    width: 397px;
    height: 560px;
    display: flex;
  }

  // @media (max-width: $desktops-2) {
  //   width: 203px;
  //   height: 321px;
  // }

  @media (max-width: 600px) {
    width: 180px;
    height: 280px;
    position: relative;
  }

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}

.person__texts {
  @media (min-width: $desktops-4) {
    margin-top: 60px;
  }

  @media (max-width: $desktops-3) {
    margin-top: 30px;
  }

  @media (max-width: 600px) {
    margin-top: -36px;
    position: relative;

    h3 {
      &:before {
        display: block;
      }
    }
  }
}

.person__text {
  margin: 33px 0 0 0;

  @media (min-width: $desktops-4) {
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 27px;
    letter-spacing: 0px;
    text-align: left;
    color: $color-default-white;
    text-indent: 30px;
  }

  @media (max-width: $desktops-3) {
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 19px;
    letter-spacing: 0px;
    text-align: left;
    color: $color-default-white;
    text-indent: 30px;
  }

  @media (max-width: 600px) {
    text-indent: 0;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: 0px;
    text-align: left;
  }
}

.person__quote {
  @media (min-width: $desktops) {
    margin: 20px 0 0 0;
  }

  @media (min-width: $desktops-4) {
    font-size: 20px;
    font-style: italic;
    font-weight: 400;
    line-height: 27px;
    letter-spacing: 0px;
    text-align: left;
    color: $color-default-white;
    text-indent: 30px;
  }

  @media (max-width: $desktops-3) {
    font-size: 14px;
    font-style: italic;
    font-weight: 400;
    line-height: 19px;
    letter-spacing: 0px;
    text-align: left;
    color: $color-default-white;
    text-indent: 30px;
  }

  @media (max-width: 600px) {
    margin: 10px 0 0 0;
    display: block;

    font-size: 14px;
    font-style: italic;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: 0px;
    text-align: left;
    color: $color-default-white;
    text-indent: 0;
  }
}

.person__text-additional {
  // margin: 5px 0 0 0;
  position: relative;

  @media (min-width: $desktops) {
    color: $color-default-white;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 19px;
    letter-spacing: 0px;
    text-align: left;
    margin-top: 15px;
    width: 204px;
    text-indent: 30px;
  }

  @media (min-width: $desktops-2) {
    transform: translateY(-24px);
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 19px;
    letter-spacing: 0px;
    text-align: left;
    color: $color-default-white;
    text-indent: 30px;
    width: 206px;
  }

  @media (min-width: $desktops-4) {
    transform: translateY(0);
    margin: 20px 0 0 0;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 27px;
    letter-spacing: 0px;
    text-align: left;
    color: $color-default-white;
    text-indent: 30px;
    width: 300px;
  }

  @media (max-width: $mobiles-max) {
    color: $color-default-white;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: 0px;
    text-align: left;
    margin-top: 5px;
  }
}

.person__pic-wrapper {
  @media (min-width: $desktops) {
    margin-right: 6%;
  }
}

.person__content {
  @media (min-width: $desktops) {
    display: flex;
  }
}

