.header {
  z-index: 5;
  min-height: 100px;

  @media (max-width: 600px) {
    min-height: 59px;
  }
}

.header__logo {
  width: 39%;
  margin-right: 6%;

  @media (max-width: 600px) {
    width: 88px;
    height: 25px;
  }
}

.header__menu {
  display: flex;
  justify-content: space-between;
  padding-top: 14px;
  flex-wrap: wrap;

  @media (max-width: $tablets-max) {
    width: 100%;
    display: flex;
    flex-direction: column;
  }
}

.header__menu-item {
  margin-left: 30px;

  a {
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 22px;
    color: $color-default-white;
  }

  @media (max-width: $tablets-max) {}
}

.header__contacts {
  width: 29%;
  justify-content: flex-end;
  display: flex;
  padding-top: 17px;

  @media (max-width: 1299px) {
    flex-direction: column;
    align-items: flex-end;
    justify-content: end;
  }

  @media (max-width: $tablets-max) {
    background: #e5e5e5;
    width: 100%;
    align-items: flex-start;
    padding-top: 0;

    a {
      padding: 15px;
      font-size: 16px;
      font-style: normal;
      font-weight: 900;
      line-height: 22px;
      letter-spacing: 0.055em;
    }
  }
}

.header__top {
  display: flex;
  justify-content: space-between;
  padding-top: 36px;
  position: relative;

  &:after {
    position: absolute;
    content: "";
    width: 100%;
    height: 1px;
    background-color: $color-default-white;
    top: 100px;
    left: 0;

    @media (max-width: 600px) {
      top: 60px;
    }
  }

  @media (max-width: 600px) {
    padding-top: 22px;
  }
}

.header__menu-toggle {
  position: absolute;
  width: 29px;
  height: 27px;
  display: flex;
  background-color: $color-red;
  align-items: center;
  justify-content: center;
  transition: $transition-settings;
  right: 25px;
  top: 30px;

  &:before {
    content: "";
    position: absolute;
    width: 2px;
    height: 20px;
    background-color: $color-white;
    transform: rotate(45deg);
    transition: $transition-settings;
  }

  &:after {
    content: "";
    position: absolute;
    width: 2px;
    height: 20px;
    background-color: $color-white;
    transform: rotate(-45deg);
    transition: $transition-settings;
  }

  &.close {
    span {
      background-color: $color-gray;
      width: 100%;
      height: 2px;
      margin-left: 1px;
    }

    @media (max-width: $tablets-max) {
      background: transparent;
    }

    &:before {
      height: 27px;
      top: -6px;
      background-color: $color-gray;
      transform: rotate(90deg);
    }

    &:after {
      height: 27px;
      bottom: -6px;
      background-color: $color-gray;
      transform: rotate(90deg);
    }
  }

  @media (min-width: $tablets-max) {
    display: none;
  }

  @media (max-width: $tablets-max) {
    display: flex;
  }
}

.header__mobile {
  position: fixed;
  width: 100%;
  z-index: 1;
  background: $color-green;
}

.header__mobile-wrapper {
  height: 100vh;
  position: fixed;
  overflow-y: scroll;
  top: 0;
  width: 100%;
}

.header__overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #17171785;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header__label {
  background-image: url("../img/city.svg");
  position: absolute;
  top: 0;
  left: 0;
  background-repeat: no-repeat;
  background-size: contain;
  left: 37%;

  @media (min-width: $desktops) {
    width: 172px;
    height: 120px;
    position: absolute;
    top: 0;
    left: 0;
    background-repeat: no-repeat;
    left: 40%;
  }

  @media (max-width: 600px) {
    background-image: url("../img/city_mob.svg");
    width: 87px;
    height: 63px;
  }

  a {
    cursor: pointer;
    font-size: 11px;
    font-style: normal;
    font-weight: 400;
    line-height: 15px;
    letter-spacing: 0px;
    text-align: center;
    color: $color-default-white;
    display: block;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

    padding-top: 18px;
  }

  span {
    padding-top: 4px;
  }
}

@media (min-width: $desktops) {
  .swiper-container {
    padding: 100px 0 !important;
    margin: -100px 0 !important;
  }
}