.introduction__wrapper {
  position: relative;

  h1 {
    @media (min-width: $desktops) {
      margin: 30px 0 0 0;
    }

    @media (min-width: $desktops-2) {
      margin: 15px 0 0 0;
    }

    @media (min-width: $desktops-4) {
      margin: 60px 0 0 0;
    }

    @media (max-width: $desktops-1) {
      width: 560px;
    }

    @media (max-width: 600px) {
      margin: 41px 0 0 0;
      z-index: 2;
      position: relative;
      width: 327px;
    }

    @media (max-width: 360px) {
      margin: 41px 0 0 0;
      z-index: 2;
      position: relative;
      width: 269px;
    }
  }
}

.introduction__inner {
  display: flex;
  position: relative;
}

.introduction__info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.introduction__title {
  width: 846px;

  @media (max-width: 600px) {
    width: auto;
  }
}

.introduction__star {
  position: absolute;

  @media (min-width: $desktops) {
    top: -5%;
    left: 57%;
    width: 125px;
    height: 125px;
  }

  @media (min-width: $desktops-2) {
    left: 53%;
    width: 180px;
    height: 180px;
  }

  @media (min-width: $desktops-4) {
    top: -90px;
    left: 50%;
  }

  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }
}

.introduction__star-rhomb {
  position: absolute;

  @media (min-width: $desktops) {
    width: 100px;
    height: 100px;
    top: 38%;
    right: -10px;
  }

  @media (min-width: $desktops-2) {
    width: 140px;
    height: 140px;
    top: 32%;
  }

  @media (min-width: $desktops-4) {
    right: 20px;
    top: 26%;
  }

  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }
}

.introduction__scheme {
  height: 304px;
  width: 486px;

  @media (max-width: 600px) {
    // height: 145px;
    // width: 230px;
    height: auto;
    width: 83%;
    max-width: 345px;
    max-height: 280px;
    margin-right: auto;
  }

  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }
}

.introduction__note {
  position: relative;
  background-color: $color-blue;
  border-radius: 50%;
  width: 288px;
  height: 288px;
  margin-bottom: 0;
  margin-top: 30px;

  @media (min-width: $desktops) {
    margin-top: 70px;
  }

  @media (min-width: $desktops-2) {
    margin-top: 40px;
  }

  @media (max-width: 600px) {
    width: 160px;
    height: 160px;
    position: relative;
    /* position: absolute; */
    /* bottom: 6px; */
    margin-right: auto;
    margin-top: 62px;
    margin-bottom: 0;
  }

  @media (max-width: 359px) {
    margin-top: 70px;
  }

  @media (min-width: $desktops-4) {
    margin-top: 110px;
  }

  @media (max-width: $desktops-3) {
    width: 215px;
    height: 215px;
    margin-bottom: 0;
  }

  @media (max-width: 600px) {
    width: 160px;
    height: 160px;
    position: relative;
    /* position: absolute; */
    /* bottom: 6px; */
    margin-right: auto;
    margin-top: 70px;
  }

  p {
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 26px;
    font-size: 16px;
    line-height: 18px;
    color: $color-default-white;
    text-align: center;

    position: absolute;
    padding: 0;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%) rotate(-8deg);
    width: 80%;

    @media (max-width: 600px) {
      font-size: 12px;
      font-style: normal;
      transform: rotate(0);
      padding: 60px 5px;
      text-align: center;
      line-height: 14px;
    }

    @media (min-width: $desktops-4) {
      font-size: 20px;
      line-height: 20px;
    }

    @media (max-width: $desktops-3) {
      font-size: 16px;
      line-height: 18px;
    }

    @media (max-width: 600px) {
      font-size: 12px;
      font-style: normal;
      transform: translate(-50%, -50%) rotate(0);
      text-align: center;
      line-height: 14px;
    }
  }
}

.introduction__pictures {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-grow: 1;
  margin-top: 0;

  @media (max-width: 600px) {
    margin-top: -15px;
  }
}

.introduction__painting {
  height: auto;

  @media (min-width: $desktops) {
    width: 47vw;
    margin: 0;
    position: absolute;
    right: 0;
    // bottom: -60px;
    bottom: -80px;
  }

  @media (min-width: $desktops-2) {
    width: 43vw;
    bottom: -130px;
    right: 10px;
  }

  @media (min-width: $desktops-4) {
    width: 44vw;
    bottom: -100px;
    right: 0;
  }

  @media (min-width: 2300px) {
    bottom: -55%;
    right: 0;
  }

  @media (max-width: 600px) {
    height: 246px;
    width: 100%;
    position: absolute;
    bottom: 50px;
  }

  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }
}