.next {
  @media (max-width: 600px) {
    margin-top: 70px;
    padding-bottom: 30px;
  }

  h3 {
    @media (max-width: 600px) {
      color: $color-default-white;
      font-size: 28px;
      font-style: normal;
      font-weight: 500;
      line-height: 29px;
      letter-spacing: 0px;
      text-align: right;

      &:before {
        display: none;
      }
    }
  }

  span {
    @media (max-width: 600px) {
      color: $color-default-white;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 15px;
      letter-spacing: 0px;
      text-align: right;

      margin-top: 15px;
      display: block;
    }
  }
}

.next__picture {
  width: 100%;
  height: auto;
  // background: linear-gradient(360deg, #98B169 9.21%, rgba(152, 177, 105, 0) 95.26%);
  background-image: linear-gradient(360deg,
      #98b169 9.21%,
      rgba(152, 177, 105, 0) 95.26%),
    url("../img/kotelnicheskaya.png");
  background-repeat: no-repeat;
  min-height: 204px;
  background-position: center;

  @media (max-width: 600px) {
    margin-top: 40px;
    width: 100%;
    height: auto;
    // background: linear-gradient(360deg, #98B169 9.21%, rgba(152, 177, 105, 0) 95.26%);
    background-image: linear-gradient(360deg,
        #98b169 9.21%,
        rgba(152, 177, 105, 0) 95.26%),
      url("../img/kotelnicheskaya.png");
    background-repeat: no-repeat;
    min-height: 204px;
    background-position: center;
    position: relative;

    &:before {
      position: absolute;
      content: "";
      width: 160px;
      height: 160px;
      top: -40px;
      left: 0;
      background-image: url('../img/star_next.png');
    }

    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }
}

.next__inner {
  position: relative;
}

.next__content {
  @media (max-width: 600px) {
    padding-right: 20%;
    padding-top: 38px;

    &:after {
      position: absolute;
      content: "";
      background-image: url("../img/ellipse.png");
      background-repeat: no-repeat;
      right: 0;
      top: 0;
      width: 240px;
      height: 320px;
      z-index: -1;
    }
  }
}
