.history {
  @media (max-width: 600px) {
    margin-top: 50px;
  }

  @media (max-width: 359px) {
    margin-top: 40px;
  }

  h2 {
    transform: rotate(-90deg);
    color: $color-default-white;
    margin: 50px 0 0 0;

    @media (min-width: $desktops) {
      margin: 40px 0 0 0;
    }

    writing-mode: vertical-rl;
    transform: rotate(-180deg);
    color: #ffffff;

    @media (max-width: 600px) {
      display: none;
    }
  }
}

.history__wrapper {
  position: relative;
  display: flex;

  @media (min-width: $desktops) {
    &:before {
      position: absolute;
      content: "";
      bottom: -60px;
      left: 32%;
      width: 145px;
      height: 145px;
      background-image: url("../img/star_gold.png");
      background-size: contain;

      @media (min-width: $desktops-2) {
        left: 23%;
        bottom: -80px;
      }

      @media (min-width: $desktops-2) {
        left: 28%;
        bottom: -80px;
        width: 200px;
        height: 200px;
      }
    }
  }

  @media (max-width: 600px) {
    &:after {
      display: none;
    }
  }
}

.history__inner {
  display: flex;
  margin-top: 50px;

  @media (min-width: $desktops) {
    margin-top: 40px;
  }

  @media (min-width: $desktops-4) {
    margin-top: 50px;
  }

  @media (max-width: 600px) {
    display: flex;
    flex-direction: column;
    padding-top: 0;
    margin-top: 0;
  }

  p {
    text-indent: 30px;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 27px;
    letter-spacing: 0px;
    text-align: left;
    color: $color-default-white;
    margin: 0 0 6% 0;

    @media (min-width: $desktops) {
      font-size: 14px;
      line-height: 20px;
    }

    @media (min-width: $desktops-4) {
      font-size: 20px;
      line-height: 27px;
    }

    @media (max-width: 600px) {
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 18px;
      letter-spacing: 0px;
      text-align: left;
      margin: 0;
    }
  }
}

.history__info {
  margin-left: 3%;
  margin-right: 6%;
  width: 31%;

  @media (min-width: $desktops) {
    // min-width: 330px;
    min-width: 32vw;
  }

  @media (min-width: $desktops-2) {
    // min-width: 375px;
    min-width: 28vw;
  }

  @media (min-width: $desktops-4) {
    // min-width: 528px;
    min-width: 28vw;
  }

  @media (max-width: 600px) {
    width: 100%;
    margin: 0;
  }
}

.history__details {
  width: auto;
  flex-grow: 1;

  @media (max-width: 600px) {
    width: 100%;
  }
}

.history__plan {
  height: auto;
  width: 405px;
  margin-top: 60px;

  @media (min-width: $desktops) {
    margin-top: 45px;
    width: 240px;
  }

  @media (min-width: $desktops-2) {
    margin-top: 45px;
    width: 270px;
  }

  @media (min-width: $desktops-4) {
    margin-top: 65px;
    width: 405px;
  }

  @media (max-width: 600px) {
    display: none;
  }

  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }
}

.history__video {
  width: 100%;
  position: relative;

  @media (min-width: $desktops) {
    // width: 435px;
    width: 89%;
  }

  @media (min-width: $desktops-2) {
    // width: 545px;
    width: 84%;
  }

  @media (min-width: $desktops-4) {
    // width: 864px;
    width: 86%;
  }

  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }

  @media (max-width: 600px) {
    // height: 201px;
    position: relative;
    height: auto;
    margin-top: 20px;

    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
      min-height: 201px;
    }
  }
}

.history__texts {
  p {
    text-indent: 30px;

    @media (max-width: 600px) {
      text-indent: 0;
    }
  }
}

.history__house-text {
  max-width: 910px;

  h3 {
    margin: 20px 0 30px;
    position: relative;

    @media (min-width: $desktops) {
      &:before {
        content: url("../img/heading.svg");
        position: absolute;
        left: -30px;
        top: -14px;
        z-index: -1;
      }
    }

    @media (min-width: $desktops) {
      font-size: 25px;
      line-height: 25px;
    }

    @media (min-width: $desktops-4) {
      font-size: 30px;
      line-height: 30px;
    }

    @media (max-width: 600px) {
      margin: 15px 0 25px;
    }
  }

  p {
    text-indent: 30px;

    &:first-of-type {
      margin-bottom: 0;
    }

    @media (max-width: 600px) {
      text-indent: 0;

      &:first-of-type {
        margin-bottom: 70px;
      }
    }
  }
}
