.neighbour {
  margin-top: 70px;
  h3 {
    @media (max-width: 600px) {
      font-size: 25px;
      font-style: normal;
      font-weight: 400;
      line-height: 28px;
      letter-spacing: -1px;
      text-align: left;
      text-indent: 10px;
      margin: 0 0 10px 0;

      &:before {
        display: none;
      }
    }
  }

  @media (max-width: 600px) {
    margin-top: 50px;
  }
}

.neighbour__text {
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: 0px;
  text-align: left;
  color: $color-default-white;
}

.neighbour__picture {
  position: relative;

  &:before {
    @media (max-width: 600px) {
      position: absolute;
      content: "";
      background-image: url("../img/ornament.png");
      width: 140px;
      height: 195px;
      right: 0;
      top: -35px;
      z-index: -1;
    }
  }

  @media (max-width: 600px) {
    margin-top: 40px;
    width: 100%;
    height: auto;

    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }
}

.neighbour__additional {
  @media (max-width: 600px) {
    display: flex;
    align-items: flex-end;
    margin-top: -45px;
    p {
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 18px;
      letter-spacing: 0px;
      text-align: left;
      color: $color-default-white;
      margin: 0;
      padding: 0 0 10px 14px;
    }
  }
}
