.popularity {
  @media (min-width: $desktops) {
    &:before {
      position: absolute;
      content: "";
      background-image: url("../img/flower_1024.png");
      width: 296px;
      height: 344px;
      left: 0;
      top: -105px;
      z-index: -1;
      background-repeat: no-repeat;
    }
  }

  @media (min-width: $desktops-2) {
    &:before {
      position: absolute;
      content: "";
      background-image: url("../img/flower_1366.png");
      width: 582px;
      height: 567px;
      left: 0;
      top: -105px;
      z-index: -1;
      background-repeat: no-repeat;
    }
  }

  @media (min-width: $desktops-4) {
    &:before {
      position: absolute;
      content: "";
      background-image: url("../img/flower-big.png");
      width: 582px;
      height: 567px;
      left: 0;
      top: -105px;
      z-index: -1;
    }
  }

  @media (min-width: $desktops) {
    &:after {
      position: absolute;
      content: "";
      background-image: url("../img/line-small.png");
      width: 347px;
      height: 510px;
      left: 10px;
      top: -5px;
      z-index: -1;
      background-repeat: no-repeat;
    }
  }

  @media (min-width: $desktops-2) {
    &:after {
      position: absolute;
      content: "";
      background-image: url("../img/line-medium.png");
      width: 389px;
      height: 571px;
      left: 20px;
      top: -65px;
      z-index: -1;
      background-repeat: no-repeat;
    }
  }

  @media (min-width: $desktops-4) {
    &:after {
      position: absolute;
      content: "";
      background-image: url("../img/line-big.png");
      width: 588px;
      height: 864px;
      left: 30px;
      top: -85px;
      z-index: -1;
    }
  }

  @media (max-width: 600px) {
    padding-top: 150px;
    overflow: hidden;
    position: relative;

    &:before {
      position: absolute;
      content: "";
      background-image: url("../img/flower.png");
      width: 199px;
      height: 236px;
      left: 0;
      top: 118px;
      z-index: -1;
    }
  }
}

.popularity__texts {
  @media (min-width: $desktops-4) {
    width: 29%;
    margin-right: 16.5%;
  }

  @media (max-width: $desktops-3) {
    width: 28%;
    margin-right: 15.5%;
  }

  @media (max-width: $desktops-2) {
    width: 33%;
    margin-right: 6.5%;
  }
}

.popularity__content {
  @media (min-width: $desktops-4) {
    margin-top: 60px;
  }

  @media (max-width: $desktops-3) {
    margin-top: 30px;
  }

  @media (max-width: $desktops-2) {
    margin-top: 20px;
  }

  @media (min-width: $desktops) {
    position: relative;
    display: flex;

    h3 {
      margin: 0;
    }

    .person__pic-wrapper {
      position: relative;
    }
  }

  .person {
    @media (min-width: $desktops) {
      .person__texts {
        margin-top: 0;
      }
    }

    @media (min-width: $desktops-4) {
      width: 54.5%;
      display: flex;
      padding-right: 6%;
    }

    @media (max-width: $desktops-3) {
      width: 54.5%;
      padding-right: 4%;
      display: flex;
    }

    @media (max-width: $desktops-2) {
      width: 60.5%;
      padding-right: 4%;
    }

    @media (max-width: 600px) {
      width: 100%;
      flex-direction: column;
      padding-right: 0;
    }
  }

  .person__text {
    margin: 27px 0 0 0;
  }
}

.person__pic-wrapper {
  @media (min-width: $desktops) {
    .person__pic {
      width: 226px;
      height: 319px;
    }
  }

  @media (min-width: $desktops-2) {
    position: relative;

    .person__pic {
      width: 247px;
      height: 390px;
    }
  }

  @media (min-width: $desktops-4) {
    .person__pic {
      width: 397px;
      height: 560px;
    }
  }

  @media (max-width: 600px) {
    h3 {
      font-size: 18px;
      font-style: normal;
      font-weight: 400;
      line-height: 18px;
      letter-spacing: 0px;
      text-align: left;

      &:before {
        display: none;
      }
    }
  }
}

.popularity__text {
  @media (min-width: $desktops-4) {
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 27px;
    letter-spacing: 0px;
    text-align: left;
    color: $color-default-white;
    text-indent: 30px;
  }

  @media (max-width: $desktops-3) {
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 19px;
    letter-spacing: 0px;
    text-align: left;
    color: $color-default-white;
    text-indent: 30px;
  }

  @media (max-width: 600px) {
    text-indent: 0;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: 0px;
    text-align: left;
  }
}

.popularity__posters {
  position: relative;

  @media (max-width: 600px) {
    min-height: 315px;
  }
}

.popularity__poster {
  position: absolute;

  @media (min-width: $desktops-4) {
    width: 250px;
  }

  @media (max-width: $desktops-3) {
    width: 162px;
  }

  @media (max-width: $desktops-2) {
    width: 144px;
  }

  &--one {
    @media (min-width: $desktops-4) {
      top: 180px;
      left: 0;
    }

    @media (max-width: $desktops-3) {
      top: 115px;
    }
  }

  &--two {
    top: 50%;
    left: 50%;
    transform: translate(-25%, 10%);
  }

  &--three {
    @media (min-width: $desktops-4) {
      top: 310px;
      right: -15%;
    }

    @media (max-width: $desktops-3) {
      top: 212px;
      right: -15%;
    }
  }

  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }

  @media (max-width: 600px) {
    position: absolute;
    width: 45%;
    max-width: 142px;

    &--one {
      top: 100px;
      left: 0;
    }

    &--two {
      top: 30px;
      left: 26%;

      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }

    &--three {
      top: 140px;
      right: 0;
    }
  }
}

.popularity__persons {
  @media (min-width: $desktops-4) {
  }

  @media (max-width: $desktops-3) {
  }

  @media (max-width: $desktops-2) {
  }

  @media (min-width: $desktops) {
    position: relative;
    display: flex;
  }

  @media (max-width: 600px) {
  }
}

.popularity__text {
  margin: 27px 0 0 0;
}

.popularity__persons .person .person__pic-wrapper {
  &::before {
    display: none;

    @media (max-width: 600px) {
      display: block;
    }
  }
}

.person__pic-wrapper {
  &--esenin {
    @media (min-width: $desktops) {
      margin-top: -20px;

      .person__pic {
        width: 203px;
        height: 315px;
      }

      .person__name {
        margin-top: 10px;
      }

      &:before {
        position: absolute;
        content: "";
        background-image: url("../img/star_gold_1024.png");
        width: 140px;
        height: 140px;
        background-repeat: no-repeat;

        left: 65%;
        z-index: -1;
        bottom: -185px;
      }
    }

    @media (min-width: $desktops-2) {
      margin-top: -30px;

      .person__pic {
        width: 247px;
        height: 390px;
      }

      .person__name {
        margin-top: 10px;
      }

      &:before {
        position: absolute;
        content: "";
        background-image: url("../img/star_gold_1366.png");
        width: 160px;
        height: 160px;
        background-repeat: no-repeat;
        left: 42%;
        z-index: -1;
        bottom: -230px;
      }
    }

    @media (min-width: $desktops-4) {
      margin-top: -60px;

      .person__pic {
        width: 355px;
        height: 560px;
      }

      .person__name {
        margin-top: 10px;
      }

      &:before {
        position: absolute;
        content: "";
        background-image: url("../img/star_gold_desktop.png");
        width: 200px;
        height: 200px;
        background-repeat: no-repeat;
        left: -38%;
        top: 0;
        z-index: -1;
      }
    }
  }
}

.swiper-slide-active + .persons-slide {
  @media (min-width: $desktops) {
    left: -10vw;
  }

  @media (min-width: $desktops-2) {
    left: -15vw;
  }

  @media (min-width: $desktops-4) {
    left: -15vw;
  }

  @media (min-width: 1900px) {
    left: -15vw;
  }
}

.popularity-slide.swiper-slide-prev {
  @media (min-width: $desktops) {
    left: 13vw;
  }

  @media (min-width: $desktops-2) {
    left: 15vw;
  }

  @media (min-width: $desktops-4) {
    left: 14vw;
  }

  @media (min-width: 1900px) {
    right: 14vw;
  }
}
