.epoque-slide {
  margin: -100px 0 !important;
  padding: 100px 0 !important;
  overflow: hidden;
  transition: 0.3s all;
}

.swiper-slide-active+.epoque-slide {
  @media (min-width: $desktops) {
    left: -7%;
  }

  @media (min-width: $desktops-2) {
    left: -7%;
  }

  @media (min-width: $desktops-4) {
    left: -3%;
  }

  @media (min-width: 1900px) {
    left: -9%;
  }
}

.epoque {
  @media (max-width: 600px) {
    margin-top: 40px;
    margin-bottom: 70px;
  }

  // background-image: url('../img/roof.jpg');
  // background-size: cover;
  // background-repeat: no-repeat;
}

.epoque__inner {
  display: flex;

  @media (min-width: $desktops) {
    margin-top: 40px;
  }

  @media (min-width: $desktops-2) {
    margin-top: 30px;
  }

  @media (min-width: $desktops-4) {
    margin-top: 40px;
  }
}

.epoque__texts {
  position: relative;

  @media (min-width: $desktops) {
    width: 33vw;
    margin-right: 4vw;
  }

  @media (min-width: $desktops-2) {
    width: 26vw;
    margin-right: 3vw;
  }

  @media (min-width: $desktops-4) {
    width: 25vw;
    margin-right: 5vw;
  }

  @media (max-width: 600px) {
    position: relative;
  }

  h3 {
    margin: 20px 0 30px;
    position: relative;

    @media (min-width: $desktops) {
      &:before {
        content: url('../img/heading.svg');
        position: absolute;
        left: -26px;
        top: -20px;
        z-index: -1;
      }
    }

    @media (min-width: $desktops) {
      font-size: 25px;
      line-height: 25px;
      margin-top: 90px;
      padding-bottom: 15px;
      margin-bottom: 15px;
      border-bottom: 1px solid #fff;
    }

    @media (min-width: $desktops-4) {
      font-size: 30px;
      line-height: 30px;
      margin-top: 150px;
    }

    @media (max-width: 600px) {
      margin: 20px 0 30px;
    }
  }

  p {
    text-indent: 32px;
    font-size: 20px;
    line-height: 27px;
    font-style: normal;
    font-weight: 400;
    letter-spacing: 0px;
    text-align: left;
    color: $color-default-white;
    margin: 0;
    margin-bottom: 30px;

    @media (min-width: $desktops) {
      font-size: 14px;
      line-height: 19px;

      &:last-of-type {
        max-width: 85%;
      }
    }

    @media (min-width: $desktops-2) {
      font-size: 14px;
      line-height: 19px;
    }

    @media (min-width: $desktops-4) {
      font-size: 20px;
      line-height: 27px;
    }

    @media (max-width: 600px) {
      text-indent: 0;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 18px;
    }
  }

  @media (max-width: 600px) {
    width: 100%;
    margin-right: 0;
  }
}

.epoque__picture {
  display: none;

  top: 0;
  position: absolute;
  width: auto;
  height: 100vh;
  right: -106vw;

  @media (min-width: $desktops) {
    right: -66vw;
    width: 124vw;
  }

  @media (min-width: $desktops-2) {
    // right: -38vw;
    width: 132vw;
  }

  @media (min-width: $desktops-4) {
    width: 131vw;
  }

  @media (max-width: 600px) {
    display: block;
    position: relative;
    top: 0;
    right: 0;
    height: 240px;
    width: 100%;
  }

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;

    @media (min-width: $desktops) {
      object-fit: contain;
      width: 100%;
      height: auto;
    }
  }
}

.epoque__wrapper {
  position: relative;
}

