.epoque__preview {
  @media (max-width: 600px) {
    position: absolute;
    top: -290px;
    left: 0;
    width: 100px;
    height: 100px;
  }
}

.epoque__things {
  width: 140px;
  height: 140px;
  content: none;
  position: absolute;
  right: -10vw;
  bottom: 10px;
  z-index: 10;

  @media (min-width: $desktops) {
    content: url('../img/thing.svg');
    right: -10vw;
    bottom: 10px;
  }

  @media (min-width: $desktops-2) {
    right: -8vw;
    bottom: 0;
  }

  @media (min-width: $desktops-4) {
    width: 190px;
    height: 190px;
  }
}

.roof-slide {
  background-image: url('../img/roof.jpg');
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
  min-height: 100vh !important;
  margin: -100px 0;
  padding: 100px 0;
  transition: 0.3s all;
  left: 0;

  &.swiper-slide-prev {
    left: auto;

    @media (min-width: $desktops) {
      right: -17vw;
    }

    @media (min-width: $desktops-2) {
      right: -27vw;
    }

    @media (min-width: $desktops-4) {
      right: -27vw;
    }
  }
}

.swiper-slide-active+.roof-slide {
  @media (min-width: $desktops) {
    left: -59vw;
  }

  @media (min-width: $desktops-2) {
    left: -66vw;
  }

  @media (min-width: $desktops-4) {
    left: -66vw;
  }

  @media (min-width: 2400px) {
    left: -55vw;
  }
}