.button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  outline: none !important;
  border: none;
  transition: $transition-settings;
  cursor: pointer;
  flex-shrink: 0;

  &--toggle {
    background-color: transparent;
    width: 27px;
    position: absolute;
    width: 29px;
    height: 27px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: $transition-settings;
    right: 25px;
    top: 30px;
    padding: 0;
    z-index: 2;

    &:before {
      content: "";
      position: absolute;
      height: 3px;
      width: 50px;

      background-color: $color-white;
      transform: rotate(45deg);
      transition: $transition-settings;
    }

    &:after {
      content: "";
      position: absolute;
      height: 3px;
      width: 50px;

      background-color: $color-white;
      transform: rotate(-45deg);
      transition: $transition-settings;
    }

    &.close {
      &:before {
        height: 3px;
        width: 50px;
        top: 0;
        right: -4px;
        transform: rotate(180deg);
      }

      &:after {
        height: 3px;
        width: 50px;
        top: 8px;
        right: -4px;
        transform: rotate(180deg);
      }
    }

    @media (max-width: $tablets-max) {
      display: flex;
    }
  }

  &--round {
    border-radius: 50%;
    background-color: $color-blue;
    color: $color-default-white;

    @media (max-width: 600px) {
      font-size: 12px;
      font-style: normal;
      font-weight: 500;
      line-height: 13px;
      letter-spacing: 0px;
      text-align: center;
    }
  }

  &--epoque {
    position: absolute;
    top: -75px;
    right: 0;
    padding: 30px 0;
    display: flex;
    flex-direction: column;

    @media (max-width: 600px) {
      width: 110px;
      height: 110px;
      justify-content: space-between;
      padding: 30px 27px;
      top: -112px;
      left: 0;
      z-index: 1;
    }

    svg {
      width: 18px;
      height: 18px;
      margin-bottom: 5px;
    }
  }

  &--person {
    // top: -114px;
    // left: 0;
    // z-index: 2;

    span {
      text-align: center;
      text-indent: 0;
    }

    @media (min-width: $desktops) {
      width: 140px;
      height: 140px;
      top: -55px;
      right: -70%;
    }

    @media (min-width: $desktops-2) {
      width: 140px;
      height: 140px;

      top: -50px;
      right: -170px;
      left: auto;
    }
    @media (min-width: $desktops-4) {
      width: 190px;
      height: 190px;

      top: -65px;
      right: -225px;
      left: auto;
    }
  }

  &--neighbour {
    position: relative;
    top: 0;
    left: 0;
  }

  &--play {
    background: transparent;
    top: 36px;
    position: absolute;

    margin: auto;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    &:before {
      background-image: url("../img/doodle.png");
      position: absolute;
      content: "";
      width: 113px;
      height: 106px;
      top: -35px;
      left: -10px;
    }
  }

  &--play-big {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    @media (min-width: $desktops) {
      width: 63px;
      height: 72px;
      background-image: url("../img/play_1024.svg");
      background-repeat: no-repeat;
    }

    @media (min-width: $desktops-2) {
      width: 79px;
      height: 90px;
      background-image: url("../img/play_1366.svg");
      background-repeat: no-repeat;
    }

    @media (min-width: $desktops-4) {
      width: 126px;
      height: 144px;
      background-image: url("../img/play_big.svg");
      background-repeat: no-repeat;
    }
    &:before {
      display: none;
    }
  }
}
