// .statue {}

.statue__big {
  @media (min-width: $desktops) {
    position: absolute;
    background-image: url("../img/statue_1920.jpg");
    background-repeat: no-repeat;
    background-position: center;

    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    height: 100vh !important;
    margin: -100px 37.5%;
    padding: 100px 0;
    width: 589px;

    z-index: -1;
  }

  @media (min-width: $desktops-2) {
    position: absolute;
    background-image: url("../img/statue_1920.jpg");
    background-repeat: no-repeat;
    background-position: center;

    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    height: 100vh !important;
    margin: -100px 29.5%;
    padding: 100px 0;
    width: 546px;
  }

  @media (min-width: $desktops-4) {
    position: absolute;
    background-image: url("../img/statue_1920.jpg");
    background-repeat: no-repeat;
    background-position: center;

    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    height: 100vh !important;
    margin: -100px 32.5%;
    padding: 100px 0;
    width: 766px;
  }
}

.statue__content {
  @media (min-width: $desktops) {
    position: relative;
    display: flex;
    flex-direction: column;

    padding-top: 114px;
    margin-left: 4%;
    width: 241px;
  }

  @media (min-width: $desktops-2) {
    display: flex;
    flex-direction: column;

    padding-top: 76px;
    margin-left: 3%;
    width: 270px;
  }

  @media (min-width: $desktops-4) {
    display: flex;
    flex-direction: column;

    padding-top: 120px;
    margin-left: 3.5%;
    width: 409px;
  }
}

.statue__pic {
  @media (min-width: $desktops) {
    width: 241px;
    height: 241px;

    img {
      border-radius: 50%;
      object-fit: contain;
      width: 100%;
      height: 100%;
    }
  }

  @media (min-width: $desktops-2) {
    width: 270px;
    height: 270px;

    img {
      border-radius: 50%;
      object-fit: contain;
      width: 100%;
      height: 100%;
    }
  }

  @media (min-width: $desktops-4) {
    width: 409px;
    height: 409px;

    img {
      border-radius: 50%;
      object-fit: contain;
      width: 100%;
      height: 100%;
    }
  }
}

.statue__text {
  @media (min-width: $desktops) {
    color: $color-default-white;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 19px;
    letter-spacing: 0px;
    text-align: left;

    padding-left: 0;
    padding-top: 10px;
    text-indent: 30px;
    margin-left: -10px;
  }

  @media (min-width: $desktops-2) {
    color: $color-default-white;
    font-family: Euclid Flex;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 19px;
    letter-spacing: 0px;
    text-align: left;


    padding-left: 4px;
    padding-top: 10px;
    text-indent: 30px;
  }

  @media (min-width: $desktops-4) {
    color: $color-default-white;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 27px;
    letter-spacing: 0px;
    text-align: left;

    padding-left: 36px;
    padding-top: 10px;
    text-indent: 30px;
  }
}