.theater {
  @media (min-width: $desktops) {
    margin-top: 30px;
    position: relative;

    &:before {
      content: '';
      position: absolute;
      background-image: url('../img/video-ellipse.svg');
      background-repeat: no-repeat;
      background-size: contain;
      width: 150px;
      height: 140px;
      top: 10px;
      left: 8vw;
    }
  }

  @media (min-width: $desktops-2) {
    &:before {
      left: 21vw;
      top: 0;
    }
  }

  @media (min-width: $desktops-4) {
    margin-top: 70px;

    &:before {
      width: 176px;
      height: 163px;
      left: 22vw;
      top: 0;
    }
  }
}

.theater__inner {
  @media (min-width: $desktops) {
    margin-left: 20vw;
    width: 80vw;
  }

  @media (min-width: $desktops-2) {
    margin-left: 30vw;
    width: 70vw;
  }

  @media (min-width: $desktops-4) {
    margin-left: 32vw;
    width: 68vw;
    max-width: 1240px;
  }

  @media (max-width: 600px) {
    width: 100%;
    margin-top: 0;
    margin-left: 0;
  }

  h3 {
    position: relative;

    @media (min-width: $desktops) {
      font-size: 25px;
      line-height: 25px;
    }

    @media (min-width: $desktops-4) {
      font-size: 30px;
      line-height: 30px;
    }

    @media (max-width: 600px) {
      margin-bottom: 25px;
    }

    @media (min-width: $desktops) {
      margin-top: 0;
      margin-bottom: 15px;
      padding-bottom: 15px;
      border-bottom: 1px solid #fff;

      &:before {
        content: url('../img/heading.svg');
        position: absolute;
        left: -26px;
        top: -20px;
        z-index: -1;
      }
    }

    @media (min-width: $desktops-4) {
      &:before {
        content: url('../img/heading.svg');
        position: absolute;
        left: -32px;
        top: -16px;
      }
    }
  }
}

.theater__texts {
  p {
    text-indent: 32px;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 27px;
    letter-spacing: 0px;
    text-align: left;
    color: $color-default-white;
    margin: 0 0 20px 0;

    @media (min-width: $desktops) {
      font-size: 14px;
      line-height: 19px;

      &:last-of-type {
        margin-bottom: 0;
      }
    }

    @media (min-width: $desktops-4) {
      font-size: 20px;
      line-height: 27px;
      margin: 0 0 30px 0;
    }

    @media (max-width: 600px) {
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 18px;
      letter-spacing: 0px;
      text-align: left;
      text-indent: 0;
      margin-bottom: 22px;

      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }
}

.theater__pictures {
  position: relative;
  flex-grow: 1;
}

.theater__main-info {
  display: flex;

  @media (max-width: 600px) {
    flex-direction: column;
  }
}

.theater__performance {
  @media (min-width: $desktops) {
    width: 18vw;
    transform: translateY(-15%);
  }

  @media (min-width: $desktops-2) {
    width: 13vw;
    transform: translateY(-7%);
  }

  @media (min-width: 1500px) {
    transform: translateY(-18%);
  }

  @media (min-width: $desktops-4) {
    width: 15vw;
    transform: translateY(-24%);
  }

  @media (max-width: 600px) {
    width: 140px;
    height: 180px;
    margin-left: auto;
    transform: translateY(0);
    width: 140px;
    height: 180px;
    position: absolute;
    top: 90px;
    right: 0;
  }

  img {
    border-radius: 50%;
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}

.theater__director {
  @media (min-width: $desktops) {
    width: 16vw;
    position: absolute;
    top: 9vw;
    margin-left: 11vw;
  }

  @media (min-width: $desktops-2) {
    width: 13vw;
    top: 7vw;
    margin-left: 10vw;
  }

  @media (min-width: 1500px) {
    top: 5vw
  }

  @media (min-width: $desktops-4) {
    width: 15vw;
    top: 6vw;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  span {
    font-style: italic;
    font-weight: 400;
    margin-top: 0;
    color: #fff;
    display: block;

    @media (min-width: $desktops) {
      font-size: 12px;
      line-height: 16px;
    }

    @media (min-width: $desktops-4) {
      font-size: 20px;
      line-height: 27px;
    }
  }
}

.theater__text-info {
  @media (min-width: $desktops) {
    width: 38vw;
    margin-right: 3vw;
  }

  @media (min-width: $desktops-2) {
    width: 30vw;
    margin-right: 4vw;
  }

  @media (max-width: 600px) {
    width: 100%;
    margin-right: 0;
  }
}

.theater__photo-text {
  font-size: 16px;
  line-height: 20px;
  font-style: italic;
  font-weight: 400;
  letter-spacing: 0px;
  text-align: left;
  color: $color-default-white;
  text-indent: 20px;

  @media (min-width: $desktops) {
    width: 39vw;
    margin-top: 40px;
    font-size: 12px;
    line-height: 16px;
    margin-bottom: 0;
  }

  @media (min-width: $desktops-2) {
    margin-top: 50px;
  }

  @media (min-width: $desktops-4) {
    margin-top: 70px;
    font-size: 16px;
    line-height: 20px;
  }

  @media (max-width: 600px) {
    display: none;
  }
}

.theater__bottom {
  width: 100%;
  height: 1px;
  background-color: #fff;
  margin-top: 20px;
  z-index: 100;
}

.theater__photo-wrapper {
  display: flex;

  @media (min-width: $desktops) {
    margin-top: 40px;
  }

  @media (min-width: $desktops-2) {
    margin-top: 70px;
  }

  @media (min-width: $desktops-4) {
    margin-top: 100px;
  }

  @media (min-width: 2400px) {
    margin-top: 170px;
  }

  @media (max-width: 600px) {
    margin-top: 30px;
  }
}

.theater__photo-info {
  margin-right: 3vw;
  position: relative;
  z-index: -1;

  @media (min-width: $desktops) {
    width: 31vw;
    margin-right: 3vw;
  }

  @media (min-width: $desktops-2) {
    width: 31vw;
    margin-right: 3vw;
  }

  @media (min-width: $desktops-4) {
    width: 31vw;
    margin-right: 3vw;
  }

  @media (max-width: 600px) {
    width: 100%;
    margin-right: 0;
  }

  p {
    color: $color-default-white;
    font-size: 16px;
    font-style: italic;
    font-weight: 400;
    line-height: 19px;
    letter-spacing: 0px;
    text-align: left;
    margin-bottom: 20px;

    @media (min-width: $desktops) {
      font-size: 12px;
      line-height: 16px;
      margin-bottom: 0;
    }

    @media (min-width: $desktops) {
      margin-bottom: 10px;
    }

    @media (min-width: $desktops-4) {
      font-size: 16px;
      line-height: 19px;
    }

    @media (max-width: 600px) {
      font-size: 12px;
      font-style: italic;
      font-weight: 400;
      line-height: 16px;
      margin: 0 0 6px 0;
    }
  }
}

.theater__scene-photo {
  width: 100%;

  @media (min-width: $desktops) {
    max-height: 130px;
  }

  @media (max-width: 600px) {
    height: auto;
    width: 85%;

    img {
      min-height: 120px;
    }
  }

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}

.theater-slide {
  position: relative;

  &::before {
    position: absolute;
    content: url("../img/flower.svg");
    top: -100px;
    right: 0;
    z-index: -1;
  }

  &::after {
    position: absolute;
    content: url("../img/line-ellipse.svg");
    top: 0;
    right: 0;
    z-index: -1;
  }

  @media (min-width: $desktops-4) {
    &::before {
      top: -15px;
      right: 70px;
      transform: scale(1.5);
    }

    &::after {
      top: 60px;
      right: 50px;
      transform: scale(1.5);
    }
  }
}