.modal {
  z-index: 50;
  margin: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.modal__body {
  background: #ffffff;
  box-shadow: 2px 2px 20px 1px;
  overflow-x: auto;
  width: 604px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 3px;

  @media (max-width: $tablets-max) {
    width: 60%;
  }

  @media (max-width: $mobiles-max) {
    min-width: 328px;
    width: 56%;
  }
}
