.socials {
  display: flex;

  @media (max-width: 600px) {
    width: 100%;
    justify-content: center;
  }
}

.socials__item {
  @media (max-width: 600px) {
    width: 45px;
    height: 45px;
    border-radius: 50%;
    background-color: $color-blue;
    margin-right: 5%;
    text-align: center;
    padding: 10px 0;

    &:last-of-type {
      margin-right: 0;
    }

    a {
      text-align: center;
      color: $color-default-white;
      font-size: 15px;
      font-style: normal;
      font-weight: 500;
      line-height: 17px;
      letter-spacing: 0px;
      text-align: center;
    }
  }
}
