.skyscraper {
  @media (max-width: 600px) {
    margin-top: 70px;
  }
}

.skyscraper__content {
  @media (max-width: 600px) {
    p {
      font-size: 12px;
      font-style: normal;
      font-weight: 400;
      line-height: 16px;
      letter-spacing: 0px;
      text-align: center;
      color: $color-default-white;
      // margin: 10px 0 0 0;
    }
  }
}

.skyscraper__picture {
  @media (max-width: 600px) {
    width: 100%;
    height: auto;

    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }
}
