/* stylelint-disable */
html {
  box-sizing: border-box;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  outline: none !important;
}

.wrapper {
  position: relative;

  display: flex;
  flex-direction: column;
  min-height: 100vh;
  // @media (max-width: $mobiles-max) {
  //   overflow-x: hidden;
  // }

  @media (min-width: $desktops) {
    min-height: 700px;
    max-height: 100vh;
  }

  @media (min-width: $desktops-4) {
    min-height: 970px;
  }

  .header,
  .footer {
    flex-shrink: 0;
  }

  main {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}

body {
  font-family: "Euclid Flex", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 140%;
  margin: 0 auto;
  height: 100%;
  color: $font-base-color;
  background-color: $color-green;
}

img {
  max-width: 100%;
  height: auto;
}

a,
button {
  cursor: pointer;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);

  &:focus {
    outline: none !important;
  }
}

h1 {
  font-size: 150px;
  font-style: normal;
  font-weight: 400;
  line-height: 150px;
  letter-spacing: -2px;
  text-align: left;
  color: $color-default-white;

  @media (max-width: 1679px) {
    font-size: 120px;
    line-height: 120px;
  }

  @media (max-width: 1279px) {
    font-size: 100px;
    line-height: 100px;
  }

  @media (max-width: $tablets-max) {
    font-size: 100px;
    line-height: 100px;
  }

  @media (max-width: 600px) {
    font-size: 60px;
    font-style: normal;
    font-weight: 400;
    line-height: 60px;
    letter-spacing: -2px;
    text-align: left;
  }

  @media (max-width: 359px) {
    font-size: 50px;
    font-style: normal;
    font-weight: 400;
    line-height: 50px;
    letter-spacing: -2px;
    text-align: left;
  }
}

h2 {
  font-family: "Euclid Flex", sans-serif;
  font-size: 40px;
  font-style: normal;
  font-weight: 400;
  line-height: 40px;
  letter-spacing: 0em;
  text-align: right;
  color: $color-default-white;

  @media (max-width: $tablets-max) {
    margin-bottom: 20px;
    font-size: 22px;
    line-height: 120%;
  }
}

h3 {
  font-family: "Euclid Flex", sans-serif;
  font-size: 30px;
  font-style: normal;
  font-weight: 400;
  line-height: 30px;
  letter-spacing: 0px;
  text-align: left;
  color: $color-default-white;

  @media (min-width: $desktops-2) {
    font-size: 25px;
    font-style: normal;
    font-weight: 400;
    line-height: 25px;
    letter-spacing: 0px;
    text-align: left;
  }

  @media (min-width: $desktops-4) {
    color: $color-default-white;
    font-size: 30px;
    font-style: normal;
    font-weight: 400;
    line-height: 30px;
    letter-spacing: 0px;
    text-align: left;
  }

  @media (max-width: $desktops-2) {
    font-size: 25px;
    font-style: normal;
    font-weight: 400;
    line-height: 25px;
    letter-spacing: 0px;
    text-align: left;
  }

  @media (max-width: $tablets-max) {
    margin-bottom: 20px;
    font-size: 22px;
    line-height: 120%;
  }

  @media (max-width: 600px) {
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: 0px;
    text-align: left;
    margin: 0;
    position: relative;

    &:before {
      position: absolute;
      content: "";
      background-image: url("../img/line-heading.png");
      left: -20px;
      top: -8px;
      background-repeat: no-repeat;
      width: 227px;
      height: 60px;
    }
  }
}

h5 {
  font-family: "Euclid Flex", sans-serif;
  font-weight: 800;
  font-size: 20px;
  line-height: 130%;
  margin: 0;
}

h6 {
  font-family: "Euclid Flex", sans-serif;
  font-size: 18px;
  line-height: 22px;
  margin: 0;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

p {
  margin: 0 0 20px 0;
}

.hidden {
  display: none i !important;
}

.show {
  display: block i !important;
}

[placeholder] {
  color: $color-placeholder;
  opacity: 1;
  transition: opacity 0.3s ease;
}

:-webkit-input-placeholder {
  color: $color-placeholder;
  opacity: 1;
  -webkit-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

:-moz-placeholder {
  color: $color-placeholder;
  opacity: 1;
  -moz-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

::-moz-placeholder {
  color: $color-placeholder;
  opacity: 1;
  -moz-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

:-ms-input-placeholder {
  color: $color-placeholder;
  opacity: 1;
  -ms-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

:focus[placeholder] {
  opacity: 0;
  outline: none;
  transition: opacity 0.3s ease;
}

:focus::-webkit-input-placeholder {
  opacity: 0;
  outline: none;
  -webkit-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

:focus::-moz-placeholder {
  opacity: 0;
  outline: none;
  -moz-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

:focus:-moz-placeholder {
  opacity: 0;
  outline: none;
  -moz-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

:focus:-ms-input-placeholder {
  opacity: 0;
  outline: none;
  -ms-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

a {
  color: $color-black;
  text-decoration: none;
  transition: all 0.3s;
}

input,
textarea {
  color: #f4f4f4;
  opacity: 1 !important;
}

table {
  width: 100%;
  border-collapse: collapse;
}

.container {
  width: 100%;
  max-width: $container;
  margin-right: auto;
  margin-left: auto;
  padding-right: 25px;
  padding-left: 25px;

  @media (max-width: 1679px) {
    padding-right: 66px;
    padding-left: 66px;
  }

  @media (max-width: 1279px) {
    padding-right: 51px;
    padding-left: 51px;
  }

  @media (max-width: $tablets-max) {
    padding-right: 25px;
    padding-left: 25px;
  }

  @media (max-width: $tablets-max) {
    max-width: 100%;
  }

  @media (max-width: $mobiles-max) {
    padding-right: 20px;
    padding-left: 20px;
  }
}

.v-select,
.vs__dropdown-toggle,
.v-select input {
  cursor: pointer !important;
}

#app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.error {
  border: 2px solid $color-red !important;
}

.grecaptcha-badge {
  display: none;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.scroll-off {
  overflow: hidden;
}

.swiper-slide {
  height: auto !important;

  section {
    height: 100%;
  }
}